}

//   base-typeahead
.rbt-token {
  color: var(--theme-deafult) !important;
  background-color: rgba($primary-color, 0.1) !important;

  .rbt-token-remove-button {
    top: 1px !important;
  }
}
.rbt-aux {
  .rbt-close {
    margin-top: 0;
  }
}

// time-picker
.react_times_button {
  .wrapper {
    display: block;
  }
}

.basic-calendar {
  .fc-scrollgrid {
    a {
      color: var(--body-font-color);
    }
  }
  .calendar-basic #external-events {
    background-color: $white;
  }
}

.time_picker_wrapper2 {
  display: flex;

  .time_picker_container {
    width: 44%;
  }

  .gap {
    padding: 0 5px;
  }
}

// toastr
.Toastify__close-button {
  font-weight: 500;

  svg {
    stroke: $white !important;
    fill: $white !important;
  }
}

.Toastify__toast--success {
  background: lighten($success-color, 15%) !important;
  color: $white !important;
  font-weight: 500;

  svg {
    fill: transparent !important;
    stroke: $white !important;
  }

  .Toastify__progress-bar {
    background: lighten($success-color, 35%) !important;
  }
}

.Toastify__toast--info {
  background: lighten($info-color, 15%) !important;
  color: $white !important;

  svg {
    fill: transparent !important;
    stroke: $white !important;
  }

  .Toastify__progress-bar {
    background: lighten($info-color, 35%) !important;
  }
}

.Toastify__toast--warning {
  background: lighten($warning-color, 5%) !important;
  color: $white !important;

  svg {
    fill: transparent !important;
    stroke: $white !important;
  }

  .Toastify__progress-bar {
    background: lighten($warning-color, 15%) !important;
  }
}

.Toastify__toast--error {
  background: lighten($danger-color, 10%) !important;
  color: $white !important;
  font-weight: 500;

  svg {
    fill: transparent !important;
    stroke: $white !important;
  }

  .Toastify__progress-bar {
    background: lighten($danger-color, 20%) !important;
  }
}

// draggable card
.draggable-card {
  > div {
    > div {
      width: 100%;
      display: flex;

      .react-kanban-column {
        width: 100%;
      }

      .react-kanban-card {
        min-width: 100%;
        max-width: 100%;
      }
    }
  }
}

.ui-sortable {
  .react-kanban-board {
    div[data-rbd-droppable-id='board-droppable'] {
      width: 100%;
      display: flex;
    }

    .react-kanban-column {
      width: 100%;
    }

    .react-kanban-card {
      max-width: 100%;
    }
  }
}

.jkanban-container {
  .custom-board {
    .kanban-drag {
      .react-kanban-column {
        &:nth-child(2) {
          div[data-rbd-drag-handle-context-id='1'] {
            background-color: $warning-color;

            ~ div {
              background-color: rgba($warning-color, 0.3);
            }
          }
        }

        &:nth-child(3) {
          div[data-rbd-drag-handle-context-id='1'] {
            background-color: $success-color;

            ~ div {
              background-color: rgba($success-color, 0.3);
            }
          }
        }
      }

      div[data-rbd-drag-handle-context-id='1'] {
        background-color: $info-color;
        color: $white;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;

        ~ div {
          background-color: rgba($info-color, 0.3);
          border-bottom-left-radius: 15px;
          border-bottom-right-radius: 15px;
        }

        span {
          font-weight: 500;
        }

        span.badge {
          font-weight: 400;
        }
      }
    }
  }

  .kanban-container {
    display: flex;

    .kanban-board {
      width: 100%;
    }

    .kanban-item {
      .customers {
        ul.simple-list {
          li {
            background-color: $transparent-color;

            + li {
              img {
                margin: 0;
              }
            }
          }
        }
      }

      &:focus {
        outline: none;
      }

      .kanban-box {
        border: 1px solid $light-semi-gray;
        background-color: $white !important;
        color: $theme-body-font-color;

        &:focus {
          outline: none;
        }
      }
    }

    .kanban-drag {
      width: 100%;

      .react-kanban-column-header {
        font-weight: 500;

        &:focus {
          outline: none;
        }

        > div {
          &:focus {
            outline: none;
          }
        }
      }

      > div {
        padding: 0;

        > div {
          display: flex;
          width: 100%;
          margin-bottom: -25px;

          .react-kanban-column {
            width: 98%;
            background-color: $body-color;
            border-radius: 8px;
            padding: 0;
            margin-bottom: 30px;

            > div {
              width: 100%;
              padding: 20px;
              font-size: 16px;
              &[data-rbd-drag-handle-context-id='0'],
              &[data-rbd-drag-handle-context-id='2'] {
                border-bottom: 1px solid $light-semi-gray;
              }
              p {
                font-weight: 400;
              }

              > div {
                padding-bottom: 0;
              }
            }

            .react-kanban-board {
              > div {
                width: 100%;
              }

              + .react-kanban-board {
                margin-top: 20px;
              }
            }
          }
        }
      }
    }
  }

  .react-kanban-column {
    background-color: $body-color;

    > div {
      > div {
        > div {
          display: block !important;
        }
      }
    }

    div[tabindex='0'] {
      + div[tabindex='0'] {
        margin-top: 20px;
      }
    }
  }
}

//DragNDrop
.ui-sortable {
  .container {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: var(--theme-deafult);

    div {
      padding: 10px;
      background-color: darken($primary-color, 1%);
      transition: opacity 0.4s ease-in-out;
      color: $white;
    }

    div {
      cursor: move;
      cursor: grab;
      cursor: -moz-grab;
      cursor: -webkit-grab;
      margin-bottom: 10px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &.ex-moved {
      background-color: #e74c3c;
    }

    &.ex-over {
      background-color: var(--theme-deafult);
    }

    &:nth-child(odd) {
      background-color: var(--theme-deafult);
    }
  }
}

.gh-fork {
  position: fixed;
  top: 0;
  right: 0;
  border: 0;
}

.gu-mirror {
  padding: 10px;
  background-color: darken($primary-color, 5%);
  transition: opacity 0.4s ease-in-out;
  color: $white;
  position: fixed !important;
  margin: 0 !important;
  z-index: 9999 !important;
  opacity: 0.8;
  -ms-filter: 'progid:DXImageTransform.Microsoft.Alpha(Opacity=80)';
  filter: alpha(opacity=80);
}

.gu-hide {
  display: none !important;
}

.gu-unselectable {
  -webkit-user-select: none !important;
  -moz-user-select: none !important;
  -ms-user-select: none !important;
  user-select: none !important;
}

.gu-transit {
  opacity: 0.2;
  -ms-filter: 'progid:DXImageTransform.Microsoft.Alpha(Opacity=20)';
  filter: alpha(opacity=20);
}

.wrapper {
  display: table;
}

.gu-mirror {
  cursor: grabbing;
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
}

.handle {
  padding: 0 5px;
  margin-right: 5px;
  background-color: darken($primary-color, 15%);
  cursor: move;
}

// timeline
.vertical-timeline {
  &::before {
    background: $light-color;
  }

  .vertical-timeline-element {
    p {
      font-size: 13px !important;
    }

    .vertical-timeline-element-icon {
      color: $white;
    }

    &:first-child,
    &:nth-child(7) {
      .vertical-timeline-element-icon {
        background-color: var(--theme-deafult);
      }
    }

    &:nth-child(2) {
      .vertical-timeline-element-icon {
        background-color: $secondary-color;
      }
    }

    &:nth-child(3),
    &:last-child {
      .vertical-timeline-element-icon {
        background-color: $success-color;
      }
    }

    &:nth-child(4) {
      .vertical-timeline-element-icon {
        background-color: $info-color;
      }
    }

    &:nth-child(5) {
      .vertical-timeline-element-icon {
        background-color: $warning-color;
      }
    }

    &:nth-child(6) {
      .vertical-timeline-element-icon {
        background-color: $danger-color;
      }
    }
  }
}

.vertical-timeline-element-content {
  box-shadow: 1px 1px 2px 1px rgba($black, 0.08);
  background-color: $light;

  audio {
    width: 100%;
    margin-top: 15px;
  }

  .vertical-timeline-element-date {
    font-size: 14px !important;
  }

  .vertical-timeline-element-content-arrow {
    border-right: 7px solid $light-color;
  }
}

.vertical-timeline--two-columns {
  .vertical-timeline-element-content {
    .vertical-timeline-element-date {
      top: 10px !important;
    }
  }
}

.vertical-timeline--one-column {
  &:before {
    left: 28px !important;
  }

  .vertical-timeline-element-icon {
    width: 60px;
    height: 60px;
  }

  .vertical-timeline-element-content {
    margin-left: 90px;

    img {
      width: 500px;
    }

    .vertical-timeline-element-date {
      float: none;
      display: block;
    }
  }
}

.rdt_Table {
  .rdt_TableHead {
    .rdt_TableHeadRow {
      .rdt_TableCol {
