.page-wrapper .page-header .header-wrapper {
  .bookmark-flip {
    width: unset !important;
    box-shadow: unset !important;
    background-color: transparent !important;
    overflow: initial;
    .Typeahead-menu {
      background-color: transparent !important;
      box-shadow: none;
      left: -1px;
      max-height: 85px;
      overflow: auto;
      top: 75px;
      .ProfileCard-realName {
        width: 100%;
      }
      .ProfileCard-details {
        float: unset;
        text-align: left;
      }

      .ProfileCard {
        border: none !important;
        padding: 8px;
        position: relative;
        margin: 0;
        &:hover {
          background-color: var(--chart-border);
        }
        .realname {
          text-transform: capitalize !important;
          text-decoration: none;
        }
        .pull-right {
          margin-left: auto;
        }
      }
    }
  }
  .flip-card {
    width: 220px;
    height: 245px;
    background-color: transparent;
    .flip-card-inner {
      position: relative;
      height: 100%;
      text-align: center;
      transition: transform 0.6s;
      transform-style: preserve-3d;
      .back {
        position: absolute;
        backface-visibility: hidden;
        border-radius: 10px;
        overflow: hidden;
        box-shadow: 0 0 20px rgba(89, 102, 122, 0.1);
        width: 220px;
        height: auto;
        button {
          background-color: $transparent-color;
          color: $primary-color;
          border: none;
          &:focus {
            outline: $transparent-color;
          }
        }
      }

      .back {
        background-color: $white;
        transform: rotateY(180deg);
        li {
          &:first-child {
            height: 160px !important;
            margin-top: 0 !important;
          }
          &:last-child {
            padding: 20px 0;
            border-top: 1px solid var(--chart-border);
          }
        }
        button {
          width: 100%;
        }
        .flip-back-content {
          .Typeahead-menu {
            max-height: 85px;
          }
          input {
            border-radius: 8px;
            padding: 6px 12px;
            width: 100%;
            border: 1px solid var(--chart-border);
            &:focus {
              outline: unset;
            }
          }
        }
      }
      &.flipped {
        transform: rotateY(180deg);
        .front {
          opacity: 0;
        }
      }
    }
  }
}

.default-datepicker {
